.trainingBox{
  display: flex;
  justify-content:center;
  align-items: center;
  flex-direction: column;

  width: 300px;
  height: 200px;

  padding: 20px 10px;

  font-size: 1.3rem;

  margin-bottom: 30px;
  border-radius: 10px;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);

  cursor: pointer;
  color: white;
  background-color: #01a738ea;

  transition: 0.1s;

}
.trainingBox:hover{
  background-color: var(--button-color-hover);
  padding: 22px 10px;
}

.trainingBox p:nth-child(1){
  font-size: 3rem;
}


@media (max-width: 700px){
  .trainingBox{
    width: 100%;
  }
}