.innerLoginContainer{
    position: absolute;
    left: 50%;
    top: 25%;
    transform: translate(-50%, -50%);

    width: 500px;
    height: 200px;

    text-align: center;

    padding: 20px;

    
}

/*
* LOGIN HEADER STYLES
*/

.loginHeader .icon span{
    font-size: 2.5rem;
    color: #017828;
}

.loginHeader .label{
    font-size: 2rem;
}

/*
*  END LOGIN HEADER STYLES
*/


/*
* LOGIN BODY STYLES
*/

.loginBody{
    width: 100%;

    display: flex;
    justify-content: center;
    

    padding: 30px;
}

.loginBody .loginForm{
    position: relative;

    display: flex;
    flex-direction: column;
    width: 100%;

    animation: login-form 0.5s linear;
}

@keyframes login-form {
    from {
        opacity: 0;
        left: 5px;
    }
    to {
        opacity: 1;
        left: 0px;
    }
}

.loginBody .loginForm label{
    text-align: left;

    margin-bottom: 10px;

    margin: 5px auto;

    opacity: 0.9;

    font-size: 17px;

    width: 88%;
}

.loginBody .loginForm .inputBox{
    position: relative;
}

.loginBody .loginForm .inputBox input{
    width: 90%;
    height: 60px;

    padding: 15px;
    margin-bottom: 20px;
    
    outline: none;
    border: 1px solid #ccc;
    border-radius: 5px;

    font-size: 1.2rem;
}



.loginBody .loginForm .inputBox input:hover,
.loginBody .loginForm .inputBox input:focus{
    outline-style: auto;
    outline-color: #017828;
}

.loginBody .loginForm .loginButton button{
    width: 90%;
    height: 40px;

    background-color: #017828;
    color: white;
    font-weight: 500;
    cursor: pointer;

    border: none;
    border-radius: 5px;
    box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
 
}

.loginBody .loginForm .loginButton button:hover{
    background-color: var(--button-color-hover);
}

.alertMessage{
    display: none;

    margin-top: 20px;

    border-radius: 5px;

    background-color: #e28383;
    padding: 5px;
}

.alertMessage p{
    font-size: 1.1rem;
    opacity: 0.8;
}

.loginOpt{
    display: flex;
    justify-content: center;

    width: 90%;
    
    margin: 20px auto;
}

.loginOpt a{
    color: #017828;
}

.errorMessage{
    margin-bottom: 20px;
    color: rgb(210, 55, 55);
}

.errorMessageNewPassword{
    margin-bottom: 10px;
    color: rgb(210, 55, 55);
}

.showPassword{
    margin: 0 auto;

    display: flex;
    flex-direction: row;
    justify-content: left;

    margin-bottom: 10px;

    width: 90%;
}
.showPassword label{
    cursor: pointer;

    font-family: var(--inter);
    font-weight: bold;
    opacity: 0.8;

    user-select: none;
}

.loginContainer .innerLoginContainer .showPassword .showPass{
    width: 20px;
    height: 20px;

    cursor: pointer;

    margin-right: 10px;
    margin-bottom: 20px;
}

.back{
    position: absolute;
    left: -50px;
    top: 5px;

    text-align: center;

    margin: 0 auto;
    
    color: #017828;
    font-size: 3rem;

    cursor: pointer;
}

.back:hover{
    color: #016723;
}

.forgotPassword{
    position: relative;

    margin-bottom: 20px;

    display: none;
    align-items: center;

    background-color: rgba(242, 245, 69, 0.881);

    padding: 5px;
    
    border-radius: 20px;

    color: rgb(53, 53, 53);
}

.forgotPassword p:nth-child(1){
    font-size: 1.5rem;
}






/*
*  END LOGIN BODY STYLES
*/


/*
*  MEDIA QUERIES
*/

@media (max-width:700px){
    .loginBody .loginForm{
        width: 100%;
        padding: 0px 5px;
    }

    .loginContainer{
        background-position: 0px 550px;
        animation: none;
    }

    .loginBody .loginForm .inputBox{
        position: static;
    }
    .back{
        top: -150px;
        left: 0px;

        width: 100px;
    }
}

